.menu-terra {
  background-color: #ff7212;
  position: fixed;
  z-index: 20;
  top: 0rem;
  left: 0rem;
  height: 40px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0rem 18px;

  @include sm {
    padding: 0rem 32px;
  }

  @include xl {
    padding: 0rem 128px;
  }

  @include xl2 {
    padding: 0rem 160px;
  }

  .logo-terra {
    width: 72px;
    height: 20px;
  }

  .produtos-terra {
    font-size: 12px;
    color: #ffffff;
    border-color: #ffffff;
    width: auto;
    white-space: nowrap;
    border-style: solid;
    border-width: 0.0625rem;
    padding: 2px 16px;
  }
}

.menu {
  z-index: 20;
  background: #faa300;
  position: fixed;
  top: 2.5rem;
  left: 0rem;
  width: 100%;
  height: 4.625rem;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include sm {
    padding: 0rem 1rem;
  }

  @include xl {
    padding: 0rem 8rem;
  }

  @include xl2 {
    padding: 0rem 8.5rem;
  }

  .menu-logo {
    width: 7.875rem;
    height: 2.5rem;
    margin-left: 1rem;

    @include xl {
      margin-left: 0rem;
    }
  }

  .menu-toggle {
    display: flex;
    justify-items: center;
    align-items: center;
    height: 4.5rem;
    width: 4.5rem;

    @include xl {
      display: none;
    }

    .bars {
      height: 0.1875rem;
      transition: 0.3s;
      position: relative;
      width: 2.125rem;
      background: $white;

      &::after,
      &::before {
        height: 0.1875rem;
        content: "";
        transition: 0.3s;
        width: 2.125rem;
        background: $white;
        position: absolute;
        left: 0rem;
      }

      &::after {
        top: -0.625rem;
      }

      &::before {
        top: 0.625rem;
      }
    }
  }

  .menu-ul {
    height: calc(100vh - 4.625rem);
    transition: 0.5s;
    width: 80%;
    padding-bottom: 2rem;
    position: absolute;
    left: -100%;
    top: 100%;
    background: $lightOrange;
    overflow-y: scroll;

    @include xl {
      overflow-y: visible;
      padding-bottom: 0rem;
      position: static;
      background: transparent;
      display: flex;
      justify-content: flex-end;
      left: 0rem;
      height: 4.625rem;
    }

    .menu-li,
    .menu-li-drop {
      transition: 0.3s;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      font-weight: 700;
      height: 4.375rem;

      @include xl {
        width: auto;
        padding: 0rem 1rem;
      }

      .menu-a-button {
        background: $white;
        color: $orange;
        font-weight: 700;
        padding: 0.5rem 1rem;
        border-radius: 0.25rem;

        @include xl {
          &:hover {
            background: $grey;
          }

          &:active {
            background: $white;
          }
        }
      }

      .menu-a {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
      }

      .menu-a,
      .text-drop {
        color: $white;
        font-size: $f-base;
        font-weight: 700;

        @include xl {
          &:hover {
            color: $black;
          }
        }
      }
    }

    .menu-li-drop {
      position: relative;
      cursor: pointer;

      .drop-icon {
        margin-left: 0.5rem;
        width: 0.875rem;
      }

      @include xl {
        &:hover {
          .text-drop {
            color: $black;
          }

          .drop-icon {
            .down-icon {
              fill: $black;
            }
          }
        }
      }

      .text-dropdown {
        display: flex;
        justify-items: center;
        gap: 0.5rem;
        width: 100%;
        height: 100%;
        justify-content: center;
      }

      .drop-ul {
        -ms-overflow-style: none;
        scrollbar-width: none;
        position: absolute;
        transition: 0.3s;
        top: 100%;
        left: 50%;
        transform: translateX(-50%);
        background: $white;
        width: 200px;
        border-radius: 0.25rem;
        height: 0rem;

        visibility: hidden;
        overflow-y: auto;

        border: 1px $darkGrey solid;

        &::-webkit-scrollbar {
          display: none;
        }

        .drop-li {
          border-bottom: solid $grey 1px;
          text-align: center;
          display: flex;
          padding: 0.5rem 0rem;
          width: 100%;
          justify-content: center;
          align-items: center;

          .drop-a {
            color: $black;
            font-weight: 700;
            font-size: $f-base;

            display: flex;
            justify-content: center;
            align-items: center;

            width: 100%;
            height: 100%;

            @include xl {
              &:hover {
                background: $grey;
              }
            }
          }
        }
      }

      &.active,
      &.active-ferramentas {
        .drop-ul {
          height: 8.25rem;
          visibility: visible;
          overflow-y: auto;
        }
      }

      &.active-ferramentas {
        .drop-ul {
          height: 26.5rem;
        }
      }
    }
  }
}

.menu.active {
  .menu-toggle {
    .bars {
      background: transparent;

      &::after {
        transform: rotate(45deg);
        top: 0rem;
      }

      &::before {
        transform: rotate(-45deg);
        top: 0rem;
      }
    }
  }

  .menu-ul {
    left: 0rem;
  }
}

.slider {
  width: 100%;
  position: relative;
  display: grid;
  align-items: center;
  overflow: hidden;

  .nav-noticias {
    animation: slidescroll 50s linear infinite;
    width: 197.5rem;
    margin-top: 7.625rem;
    display: flex;
    align-items: center;

    .nav-noticias-li {
      width: 18.75rem;
      border-radius: 0.25rem;
      background: $grey;
      padding: 16px;
      height: 5rem;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      margin-right: 1rem;

      .nav-destaque {
        font-weight: 700;
        color: $orange;
      }
    }
  }
}

@keyframes slidescroll {
  0% {
    transform: translateX(0rem);
  }

  100% {
    transform: translateX(-197.5rem);
  }
}
