.generic-container {
  margin-bottom: 4rem;
}



.text-orange {
  color: $orange;
}

.categoria-text {
  font-weight: 700;
  margin: 0.5rem 0rem;
  padding: 0rem 1rem;

  @include sm {
    font-size: $f-base;
  }

  @include md {
    padding: 0rem 3rem;
  }

  @include xl2 {
    margin: auto;
    width: 75rem;
    padding: 0;
  }
}



.line-after::after {
  content: "";
  position: absolute;
  width: 5rem;
  height: 0.25rem;
  background: $orange;
  left: 0rem;
  bottom: -0.25rem;
  border-radius: 624.9375rem;
}

.justify-container {
  margin: 1rem 0rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.data-text {
  margin-left: 0.5rem;
  color: $darkGrey;
  font-size: $f-min;

  @include sm {
    font-size: $f-base;
  }
}

.clock {
  width: 1rem;
  height: 1.125rem;
}

.mobile-title {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: $f-semi;
  position: relative;
}

.mb-48px {
  margin-bottom: 3rem;
}

.cms-text {
  font-size: $f-base;
  font-weight: 700;
  color: $orange;
}

.flex {
  align-items: center;
  display: flex;
}

.noticia-img {
  height: 13.5rem;
  width: 100%;
  object-fit: cover;

  @include sm {
    height: 18.75rem;
  }

  @include md {
    height: 25rem;
  }

  @include lg {
    height: 18.75rem;
  }
}

.text-anuncio {
  color: #0ea5e9;
  font-weight: 700;
}

.leia-mais-container {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 1.5rem;

  .leia-mais-btn {
    width: 6rem;
    text-align: center;
    padding: 0.25rem 0.75rem;
    font-weight: 700;
    background: $orange;
    color: $white;
    border-radius: 0.25rem;

    @include xl {
      &:hover {
        background: $lightOrange;
      }

      &:active {
        background: $orange;
      }
    }
  }
}



.destaque-border {
  padding: 32px;
  border: solid 1px $orange;
  border-radius: 0.25rem;
}

.texto-container {
  margin: 1rem 0rem;
  position: relative;
  height: 350px;
  overflow-y: hidden;
}

.texto-noticia {
  line-height: 1.9688rem;
  font-size: $f-base;
}

.overlay-texto {
  background: linear-gradient(360deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%);
  position: absolute;
  width: 100%;
  height: 10rem;
  left: 0rem;
  bottom: 0rem;
}

.input-boletim {
  height: 2.25rem;
  border-radius: 624.9375rem;
  text-indent: 16px;
  margin: 0.5rem 0rem;

  &:focus {
    background: $grey;
    outline: none;
  }
}

.noticias,
.noticias-desktop {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @include xl {
    max-width: 100%;
  }


  .noticias-li {
    // max-width: 100%;
    padding-bottom: 1rem;
    margin-bottom: 2rem;
    border-bottom: solid $grey 0.0625rem;
    position: relative;

    @include xl {
      max-width: 100%;
    }

    .noticias-figure {
      .noticias-img {
        width: 100%;
        max-width: 100%;
        height: 9.375rem;
        border-radius: 0.25rem;
        object-fit: cover;

        @include xl {
          width: auto;
        }
      }
    }

    .noticias-topico {
      position: absolute;
      top: 0rem;
      left: 0rem;
      border-radius: 0.25rem;
      width: 6.625rem;
      height: 1.875rem;
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;
      background: $orange;
      color: $white;
      font-weight: 700;
    }

    .noticias-title {
      margin: 1rem 0rem;
      // TALVEZ APAGUE
      font-family: "Montserrat", sans-serif;
      font-weight: 700;
      font-size: $f-semi;
      position: relative;
    }
  }
}

.noticias {
  .noticias-li a {
    @include sm {
      display: flex;
    }

    @include lg {
      display: block;
    }

    @include xl {
      gap: 0.5rem;
    }

    @include xl2 {
      display: grid;
      grid-template-columns: 105px 1fr;
    }

    article {
      .noticias-text {
        margin: 1rem 0rem;
        padding-bottom: 1rem;
        font-size: $f-base;
      }
    }

    .noticias-topico {
      @include sm {
        display: block;
        text-align: left;
        margin-top: 0rem;
        position: static;
        background: transparent;
        color: $orange;
      }

      @include lg {
        padding-top: 0.25rem;
      }

      @include xl {
        padding-top: 0rem;
        font-weight: 400;
      }
    }

    .noticias-figure {
      @include sm {
        margin-top: 0rem;
        margin-bottom: 0rem;
      }

      .noticias-img {
        @include sm {
          width: 25rem;
          height: 100%;
        }

        @include lg {
          height: 6.25rem;
        }
      }
    }

    article {
      @include sm {
        width: 100%;
      }

      .noticias-topico {
        @include sm {
          font-size: $f-original;
        }
      }

      .noticias-title {
        @include sm {
          top: 0rem;
          margin: 0rem;
          font-size: $f-semi;
        }

        @include lg {
          font-size: $f-base;
        }

        @include xl {
          font-size: $f-original;
        }
      }

      .noticias-text {
        font-size: $f-original;
      }
    }

    article {
      @include sm {
        margin-left: 2rem;
      }

      @include lg {
        margin-left: 0rem;
      }
    }
  }
}

.noticias-desktop {
  @include lg {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: space-between;
  }

  @include xl {
    grid-template-columns: 1fr 1fr 1fr;
  }

  .noticias-li {
    @include lg {
      margin-right: 2rem;
    }

    .noticias-figure {
      .noticias-img {
        @include sm {
          height: 12.5rem;
        }

        @include md {
          height: 7.5rem;
        }

        @include lg {
          height: 10rem;
        }
      }
    }
  }
}

.center-container {
  width: 100%;
  text-align: center;
  margin-top: 1rem;
}

.ver-mais {
  padding: 0.5rem;
  text-decoration: underline;
  font-weight: 700;
  @include sm {
    font-size: $f-base;
  }
}

.fatos {
  margin-top: 2.25rem;

  .fatos-li {
    height: 5rem;
    border-bottom: solid $grey 1px;

    .fatos-text {
      font-weight: 700;
      font-family: "Montserrat", sans-serif;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      height: 100%;
      width: 100%;

      @include xl {
        &:hover {
          background: $grey;
        }
      }
    }
  }
}

.social-container {
  display: flex;
  align-items: center;
  margin-top: 1.5rem;
  flex-wrap: wrap;

  .social-media-a {
    margin: 0.25rem 0rem;
    padding: 0rem 0.75rem;

    @include lg {
      padding: 0rem 0.375rem;
    }

    &:first-child {
      padding-left: 0rem;
    }

    .social-media-img {
      width: 2.125rem;
      height: 2.125rem;

      @include sm {
        width: 2.875rem;
        height: 2.875rem;
      }

      @include lg {
        width: 2.125rem;
      }
    }
  }
}

.imprimir-container {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;

  .imprimir-img {
    width: 1.625rem;
    height: 1.625rem;
  }

  .imprimir-text {
    font-weight: 700;
    font-size: $f-base;
  }
}

.input-container {
  display: flex;
  flex-direction: column;
  margin-top: 1rem;

  .input-label {
    color: $darkGrey-2;
  }

  .input-field {
    margin-top: 0.125rem;
    color: $black;
    text-indent: 0.5rem;
    width: 100%;
    height: 3rem;
    background: $lightGrey;

    .option-text {
      color: $black;
    }
  }
}

.center-mb {
  text-align: center;
  margin-bottom: 2rem;
}

.investimentos-container {
  @include lg {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
  }
}

.investimentos-btn {
  margin: auto;
  margin-top: 3rem;
  display: block;
  background: $orange;
  color: $white;
  padding: 0.5rem 1rem;
  font-weight: 700;
  border-radius: 0.25rem;

  @include xl {
    &:hover {
      background: $lightOrange;
    }

    &:active {
      background: $orange;
    }
  }
}

.mt-16px {
  margin-top: 1rem;
}

.mt-36px {
  margin-top: 2.25rem;
}

.boletim-container {
  background: $orange;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  border-radius: 4px;
  padding: 1rem;

  @include lg {
    padding: 2rem;
  }

  .boletim-text {
    font-weight: 700;
    text-align: center;
    font-size: $f-semi;
    color: $white;

    @include lg {
      font-size: $f-base;
    }
  }

  .boletim-div {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin: 1rem 0rem;
  }

  .boletim-btn {
    font-weight: 700;
    background: $white;
    font-size: $f-base;
    color: $orange;
    padding: 0.5rem 1.5rem;
    border-radius: 0.25rem;

    @include xl {
      &:active {
        background: $white;
      }

      &:hover {
        background: $grey;
      }
    }
  }
}
