.layout-container {
  padding: 0rem 1rem;

  @include sm {
    padding: 0rem 2rem;
  }

  @include md {
    padding: 0rem 3rem;
  }

  @include lg {
    display: flex;
    gap: 1.875rem;
    // grid-template-columns: 1fr 255px;

    // .side-layout {
    //   width: 20rem;
    // }

    .gap-layout {
      padding-right: 1.875rem;
      flex-basis: 40.1875rem;
      flex-grow: 0;
      flex-shrink: 0;
    }

    .side-layout {
      display: flex;
      flex-direction: column;
      flex-basis: 15.9375rem;
      flex-grow: 0;
      flex-shrink: 0;
    }
  }

  @include xl {
    // padding: 0rem 8rem;
    display: flex;
    // grid-template-columns: 1fr 300px;

    // .side-layout {
    //   width: 18.75rem;
    // }

    .gap-layout {
      flex-basis: 55.25rem;
      flex-grow: 0;
      flex-shrink: 0;
    }

    .side-layout {
      width: 18.75rem;
      flex-basis: 15.9375rem;
      flex-grow: 0;
      flex-shrink: 0;
    }
  }

  @include xl2 {
    // display: grid;
    // grid-template-columns: 1fr 310px;
    margin: auto;
    width: 1200px;
    padding: 0px;

    .gap-layout {
      flex-basis: 54.375rem;
    }

    .side-layout {
      flex-basis: 18.75rem;
    }
  }
}

