.paginacao-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0px 0px 64px 0px;

  @include md {
    margin: 16px 0px;
  }

  @include lg {
    margin: 16px 0px 32px 0px;
  }

  .paginacao-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 60px;
    padding: 0px 8px;
    margin: 8px 0px;

    @include sm {
      padding: 0px 16px;
    }

  }
  
  .numeros-lista {
    display: flex;

    li {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 0px 8px;
      height: 60px;
      width: auto;

      @include sm {
        padding: 0px 16px;
      }

      a {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
        width: 100%;
      }

      .active {
        height: 32px;
        padding: 0;
        width: 32px;
        border-radius: 100px;
        color: #ffffff;
        background: $orange;
      }
    }
  }

  & > * {
    font-weight: 700;
  }




}