.side-banner {
  width: 100%;
  margin-top: 0rem;
  height: 12.5rem;

  @include sm {
    height: 18.75rem;
  }
}

.banner-text {
  margin: auto;
  height: 12.5rem;

  @include sm {
    height: 18.75rem;
  }
}

.banner-container,
.banner-container-top {
  margin: 2rem 0rem;
  background: $darkGrey;
  display: flex;
  justify-content: center;
  align-items: center;
}

.banner-container-top {
  width: 100%;
  height: 6.25rem;

  @include sm {
    height: 9.375rem;
  }

  @include md {
    height: 15.625rem;
  }

  @include xl2 {
    width: 75rem;
    padding: 0px;
    margin: 1.5rem auto;
  }
}
