.search-box {
  display: flex;
  justify-content: space-between;
  width: 100%;
  border-radius: 100px;
  height: 48px;
  margin: 32px 0px;
  padding: 0px 16px;

  @include sm {
    padding: 0px 32px;
  }

  @include sm {
    font-size: $f-base;
  }

  @include md {
    padding: 0rem 3rem;
  }

  @include lg {
    margin: 48px 0;
  }

  @include xl2 {
    margin: 48px auto;
    width: 75rem;
    padding: 0;
  }

  input {
    text-indent: 1rem;
    border-radius: 100px 0px 0px 100px;
    width: 100%;
    background: #f8fafc;

    @include sm {
      text-indent: 24px;
    }

    &:focus {
      border: none;
      outline: none;
    }
  }

  .search-btn {
    border-radius: 0px 100px 100px 0px;
    width: 6rem;
    height: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #f8fafc;

    img {
      width: 25px;
      height: 25px;
    }
  }
}

.lista-gap {
  display: flex;
  flex-direction: column;
  gap: 32px;

  @include sm {
    gap: 48px;
  }

  .lista-noticias {
    display: flex;
    flex-direction: column;
    gap: 16px;
    padding-bottom: 32px;
    border-bottom: solid 1px #dadada;

    @include sm {
      padding-bottom: 48px;
    }

    @include md {
      flex-direction: row;
    }

    .lista-noticias-img {
      width: 100%;
      height: 192px;

      @include sm {
        height: 314px;
      }

      @include md {
        width: 284px;
        height: 168px;
      }

      @include lg {
        width: 236px;
        height: 140px;
      }

      @include xl {
        width: 284px;
        height: 186px;
      }
    }

    article {
      display: flex;
      flex-direction: column;
      gap: 16px;

      .link {
        .lista-noticias-tag {
          font-weight: 700;
          color: $orange;
        }
  
        .lista-noticia-title {
          font-family: $montserrat;
          font-weight: 700;
          font-size: 24px;
          line-height: 33px;
        }

      }

      

      .flex-between {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .lista-noticia-data {
          font-size: 14px;
          color: #727272;
        }

        .lista-noticia-compartilhar {
          display: flex;
          align-items: center;
          padding: 8px;
          gap: 8px;
          position: relative;

          &.active {
            .social-pop-up {
              display: flex;
            }
          }

          .social-pop-up {
            display: none;
            background: #ffffff;
            border: #dadada solid 1px;
            padding: 8px;
            border-radius: 4px;
            height: auto;
            width: auto;
            position: absolute;
            left: 0%;
            top: 36px;
            transform: translateX(-50%);
            gap: 16px;
            

            a {
              display: flex;
              justify-content: center;
              align-items: center;
              width: 45px;
              height: 45px;

              img {
                display: flex;
                height: 100%;
                width: 100%;
              }
            }
          }

          .lista-link-compartilhar {
            font-weight: 700;
          }
        }
      }
    }
  }
}
