.social-media-img-footer {
  width: 32px;
  height: 32px;

  @include lg {
    width: 2.125rem;
    height: 2.125rem;
  }
}

footer {
  padding: 2rem 1.5rem;
  background: $black;

  @include sm {
    padding: 2rem 3rem;
  }

  @include xl2 {
    padding: 2rem 0rem;
  }

  .center-footer {
    @include xl2 {
      margin: auto;
      width: 1300px;
    }
  }

  .rodape-text-container {
    width: 100%;
    display: flex;
    justify-content: center;

    .rodape-p {
      width: 100%;
      text-align: center;
      padding-top: 1rem;
      color: $lightGrey-1;
      border-top: solid $white 1px;
    }
  }

  .footer {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

    .join-links {
      width: 100%;

      @include md {
        width: 16.25rem;
      }
    }

    .footer-sub {
      width: 100%;

      .footer-form {
        display: flex;
        justify-content: center;
        margin-bottom: 2rem;

        @include md {
          margin-bottom: 3rem;
        }
      }

      @include lg {
        width: 18.75rem;
      }

      .logo-footer {
        margin: auto;
        width: 9.125rem;
        height: 3.75rem;
      }

      .logo-text {
        margin: auto;
        text-align: center;

        @include sm {
          width: 25rem;
        }

        @include lg {
          width: auto;
        }
      }

      .social-footer {
        margin-bottom: 1.5rem;
        justify-content: space-between;

        @include sm {
          justify-content: center;
        }
      }
    }

    @include sm {
      justify-content: space-between;
    }

    @include lg {
      display: grid;
      grid-template-columns: repeat(3, minmax(0, 1fr));
      column-gap: 8rem;
    }

    @include xl2 {
      display: flex;
      width: 100%;
      gap: 0px;
    }

    .footer-text {
      color: $lightGrey-1;
    }

    .input-footer {
      border-radius: 0 0.1875rem 0.1875rem 0;
      background: #4d4d4d;

      width: 11rem;
      height: 2.25rem;
      text-indent: 1rem;
      color: white;
      &:focus {
        outline: none;
      }
    }

    .btn-inscrever {
      color: $white;
      background: $orange;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 6.375rem;
      height: 2.25rem;

      @include xl {
        &:hover {
          background: $orange;
        }
        &:active {
          background: $orange;
        }
      }
    }

    .links-container {
      margin-bottom: 0.5rem;
      width: 100%;
      text-align: left;

      @include md {
        width: 16.25rem;
        padding-bottom: 1rem;
      }

      @include xl2 {
        width: auto;
      }

      .links-title {
        color: $white;
        font-size: $f-base;
        font-weight: 700;
      }

      .links-ul {
        margin: 0.5rem 0rem;

        .links-li {
          width: 16.25rem;
          height: 2rem;
          display: flex;
          justify-content: center;
          align-items: center;

          .links-a {
            color: $lightGrey-1;
            width: 100%;
            height: 100%;
            justify-content: center;
            align-items: center;

            @include xl {
              &:hover {
                text-decoration: underline;
              }
            }
          }
        }
      }
    }
  }
}

.overlay {
  background-color: rgba($color: #000000, $alpha: 0.3);
  position: absolute;
  left: 0rem;
  top: 0rem;
  visibility: hidden;
  transition: 0.3s;
  width: 100%;
  height: 100%;
  z-index: 10;
}
